"use client";

import { useState, useEffect } from "react";
import { useAuth } from "@/contexts/AuthContext";
import {
    UserPlus,
    Search,
    MessageCircle,
    Phone,
    Video,
    MoreVertical,
    Users,
    UserX
} from "lucide-react";
import Image from "next/image";
import Link from "next/link";

interface Friend {
    _id: string;
    username: string;
    email: string;
    avatar: string;
    isOnline: boolean;
    lastSeen?: Date;
    mutualFriends?: number;
}

export default function FriendsPage() {
    const { user } = useAuth();
    const [friends, setFriends] = useState<Friend[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState("");
    const [activeTab, setActiveTab] = useState<"all" | "online">("all");

    useEffect(() => {
        fetchFriends();
    }, []);

    const fetchFriends = async () => {
        try {
            const response = await fetch("/api/friends", {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem("authToken")}`
                }
            });

            if (response.ok) {
                const data = await response.json();
                setFriends(data.friends || []);
            }
        } catch (error) {
            console.error("Error fetching friends:", error);
        } finally {
            setLoading(false);
        }
    };

    // Filter friends based on search and tab
    const filteredFriends = friends.filter(friend => {
        const matchesSearch = friend.username.toLowerCase().includes(searchQuery.toLowerCase()) ||
            friend.email.toLowerCase().includes(searchQuery.toLowerCase());
        const matchesTab = activeTab === "all" || (activeTab === "online" && friend.isOnline);

        return matchesSearch && matchesTab;
    });

    const onlineFriendsCount = friends.filter(f => f.isOnline).length;

    if (loading) {
        return <FriendsLoadingSkeleton />;
    }

    return (
        <div className="max-w-7xl mx-auto">
            {/* Header */}
            <div className="mb-8">
                <div className="flex items-center gap-3">
                    <Users className="w-8 h-8 text-primary" />
                    <h1 className="text-3xl font-bold text-base-content">Friends</h1>
                </div>
                <p className="text-base-content/60">
                    Connect and chat with your language learning partners
                </p>
            </div>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
                <div className="card bg-base-100 shadow-md">
                    <div className="card-body">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-base-content/60">Total Friends</p>
                                <p className="text-3xl font-bold">{friends.length}</p>
                            </div>
                            {/* <Users className="w-8 h-8 text-primary" /> */}
                        </div>
                    </div>
                </div>

                <div className="card bg-base-100 shadow-md">
                    <div className="card-body">
                        <div className="flex items-center justify-between">
                            <div>
                                <p className="text-base-content/60">Online Now</p>
                                <p className="text-3xl font-bold text-success">{onlineFriendsCount}</p>
                            </div>
                            <div className="relative">
                                <div className="w-8 h-8 bg-success rounded-full animate-pulse" />
                                <div className="absolute inset-0 w-8 h-8 bg-success rounded-full animate-ping opacity-75" />
                            </div>
                        </div>
                    </div>
                </div>

                <div className="card bg-base-100 shadow-md">
                    <div className="card-body">
                        <Link
                            href="/friends/add"
                            className="btn btn-primary btn-block"
                        >
                            <UserPlus className="w-5 h-5" />
                            Add Friends
                        </Link>
                    </div>
                </div>
            </div>

            {/* Search and Tabs */}
            <div className="card bg-base-100 shadow-md mb-6">
                <div className="card-body">
                    <div className="flex flex-col lg:flex-row gap-4">
                        {/* Search */}
                        <div className="flex-1">
                            <div className="relative">
                                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-base-content/40" />
                                <input
                                    type="text"
                                    placeholder="Search friends..."
                                    value={searchQuery}
                                    onChange={(e) => setSearchQuery(e.target.value)}
                                    className="input input-bordered w-full pl-10"
                                />
                            </div>
                        </div>

                        {/* Tabs */}
                        <div className="tabs tabs-boxed">
                            <button
                                className={`tab ${activeTab === "all" ? "tab-active" : ""}`}
                                onClick={() => setActiveTab("all")}
                            >
                                All Friends
                            </button>
                            <button
                                className={`tab ${activeTab === "online" ? "tab-active" : ""}`}
                                onClick={() => setActiveTab("online")}
                            >
                                Online ({onlineFriendsCount})
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            {/* Friends List or Empty State */}
            {friends.length === 0 ? (
                <EmptyFriendsState />
            ) : filteredFriends.length === 0 ? (
                <NoSearchResults searchQuery={searchQuery} />
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {filteredFriends.map((friend) => (
                        <FriendCard key={friend._id} friend={friend} />
                    ))}
                </div>
            )}
        </div>
    );
}

// Friend Card Component
function FriendCard({ friend }: { friend: Friend }) {
    const [showMenu, setShowMenu] = useState(false);

    return (
        <div className="card bg-base-100 shadow-md hover:shadow-lg transition-shadow">
            <div className="card-body">
                {/* Header with avatar and status */}
                <div className="flex items-start justify-between mb-4">
                    <div className="flex items-center gap-3">
                        <div className="relative">
                            <div className="avatar">
                                <div className="w-12 rounded-full">
                                    <Image
                                        src={friend.avatar || "/default-avatar.png"}
                                        alt={friend.username}
                                        width={48}
                                        height={48}
                                    />
                                </div>
                            </div>
                            {/* Online indicator */}
                            {friend.isOnline && (
                                <div className="absolute bottom-0 right-0 w-3 h-3 bg-success rounded-full border-2 border-base-100" />
                            )}
                        </div>

                        <div>
                            <h3 className="font-semibold text-base-content">
                                {friend.username}
                            </h3>
                            <p className="text-sm text-base-content/60">
                                {friend.isOnline ? "Online" : `Last seen ${formatLastSeen(friend.lastSeen)}`}
                            </p>
                        </div>
                    </div>

                    {/* Menu */}
                    <div className="dropdown dropdown-end">
                        <button
                            className="btn btn-ghost btn-sm btn-square"
                            onClick={() => setShowMenu(!showMenu)}
                        >
                            <MoreVertical className="w-4 h-4" />
                        </button>
                        <ul className="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52">
                            <li><a>View Profile</a></li>
                            <li><a>Block User</a></li>
                            <li><a className="text-error">Remove Friend</a></li>
                        </ul>
                    </div>
                </div>

                {/* Info */}
                <div className="text-sm text-base-content/60 mb-4">
                    {friend.mutualFriends && friend.mutualFriends > 0 && (
                        <p>{friend.mutualFriends} mutual friends</p>
                    )}
                </div>

                {/* Actions */}
                <div className="flex gap-2">
                    <Link
                        href={`/chat/${friend._id}`}
                        className="btn btn-primary btn-sm flex-1"
                    >
                        <MessageCircle className="w-4 h-4" />
                        Message
                    </Link>
                    <button className="btn btn-ghost btn-sm btn-square">
                        <Phone className="w-4 h-4" />
                    </button>
                    <button className="btn btn-ghost btn-sm btn-square">
                        <Video className="w-4 h-4" />
                    </button>
                </div>
            </div>
        </div>
    );
}

// Empty State Component
function EmptyFriendsState() {
    return (
        <div className="card bg-base-100 shadow-md">
            <div className="card-body text-center py-16">
                <div className="flex justify-center mb-6">
                    <div className="w-24 h-24 bg-base-200 rounded-full flex items-center justify-center">
                        <Users className="w-12 h-12 text-base-content/40" />
                    </div>
                </div>

                <h2 className="text-2xl font-bold mb-2">No friends yet</h2>
                <p className="text-base-content/60 mb-6 max-w-md mx-auto">
                    Start connecting with language learners around the world.
                    Add friends to practice together and improve faster!
                </p>

                <div className="flex gap-4 justify-center">
                    <Link href="/friends/add" className="btn btn-primary">
                        <UserPlus className="w-5 h-5" />
                        Find Friends
                    </Link>
                    <Link href="/friends/suggestions" className="btn btn-ghost">
                        Explore Suggestions
                    </Link>
                </div>
            </div>
        </div>
    );
}

// No Search Results Component
function NoSearchResults({ searchQuery }: { searchQuery: string }) {
    return (
        <div className="card bg-base-100 shadow-md">
            <div className="card-body text-center py-16">
                <div className="flex justify-center mb-6">
                    <div className="w-24 h-24 bg-base-200 rounded-full flex items-center justify-center">
                        <Search className="w-12 h-12 text-base-content/40" />
                    </div>
                </div>

                <h2 className="text-xl font-bold mb-2">No results found</h2>
                <p className="text-base-content/60">
                    No friends matching "{searchQuery}"
                </p>
            </div>
        </div>
    );
}

// Loading Skeleton
function FriendsLoadingSkeleton() {
    return (
        <div className="max-w-7xl mx-auto">
            <div className="animate-pulse">
                {/* Header skeleton */}
                <div className="h-8 bg-base-200 rounded w-48 mb-2"></div>
                <div className="h-4 bg-base-200 rounded w-96 mb-8"></div>

                {/* Stats skeleton */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
                    {[1, 2, 3].map((i) => (
                        <div key={i} className="card bg-base-100">
                            <div className="card-body">
                                <div className="h-24 bg-base-200 rounded"></div>
                            </div>
                        </div>
                    ))}
                </div>

                {/* List skeleton */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {[1, 2, 3, 4, 5, 6].map((i) => (
                        <div key={i} className="card bg-base-100">
                            <div className="card-body">
                                <div className="h-32 bg-base-200 rounded"></div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

// Helper function
function formatLastSeen(date?: Date): string {
    if (!date) return "recently";

    const now = new Date();
    const diff = now.getTime() - new Date(date).getTime();
    const minutes = Math.floor(diff / 60000);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);

    if (days > 0) return `${days}d ago`;
    if (hours > 0) return `${hours}h ago`;
    if (minutes > 0) return `${minutes}m ago`;
    return "just now";
}